<template>
<div class="m_r">
  <header class="top_bar">
      <a onclick="window.history.go(-1)" class="icon_back"></a>
      <h3 class="cartname">京东登录</h3>
  </header>
  <main class="user_login_box">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="2rem">
      <el-form-item label="帐号" prop="username">
        <el-input v-model="ruleForm.username" placeholder="用户名/邮箱/手机号"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input type="password" maxlength="20" v-model="ruleForm.pass" placeholder="请输入密码" v-show="pwdon"></el-input>
        <el-input type="text" v-model="ruleForm.pass" placeholder="请输入密码" v-show="!pwdon"></el-input>
        <img v-show="!pwdon" @click.prevent="dealPwd(true)" src="../assets/images/data_image_png;base….png"/>
        <img v-show="pwdon" @click.prevent="dealPwd(false)" src="../assets/images/xia.png"/>
      </el-form-item>
      <el-form-item prop="captcha">
        <el-input v-model="ruleForm.captcha" autocomplete="off" placeholder="请输入验证码"></el-input>
        <img ref="captcha" @click.prevent="getCaptcha()" class="get-verification" src="http://localhost:3000/captcha" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login('ruleForm', ruleForm.username, ruleForm.pass, ruleForm.captcha)">登录</el-button>
      </el-form-item>
    </el-form>
  </main>
</div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
  data () {
    var checkname = (rule, value, callback) => {
      if (!value) {
        callback(new Error('用户名不能为空'))
      } else {
        callback()
      }
    }
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        callback()
      }
    }
    var captcha = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入验证码'))
      } else {
        callback()
      }
    }
    return {
      pwdon: true,
      userInfo: '',
      pwd: '',
      code: '',
      captcha: '',

      ruleForm: {
        pass: '',
        username: '',
        captcha: ''
      },
      rules: {
        username: [{ validator: checkname, trigger: 'blur' }],
        pass: [{ validator: validatePass, trigger: 'blur' }],
        captcha: [{ validator: captcha, trigger: 'blur' }]
      }
    }
  },
  computed: {
    // loginPwd () {
    //   // 账号密码登录 前端校验
    //   if (!this.username) {
    //     Toast('请输入帐号！')
    //     return false
    //   } else if (!this.phoneRight) {
    //     Toast('请输入密码！')
    //     return false
    //   } else if (!this.captcha) {
    //     Toast('请输入验证码！')
    //     return true
    //   }
    // }
  },
  methods: {
    ...mapActions(['syncUserInfo']),
    dealPwd (flag) {
      // console.log(this)
      this.pwdon = flag
    },
    // 密码码登录
    async login (formName, name, phone, captcha) {
      await this.$refs[formName].validate((valid) => {
        if (valid) {
          // console.log(valid)
          this.$http.post('/login_pwd', { name, phone, captcha }).then((data) => {
            // console.log(data.data, '1111111111111')
            if (data.data.success_code === 200) {
              this.userInfo = data.data.message
              // 6. 后续的处理
              if (!this.userInfo.id) {
                // Toast(this.userInfo.message)
              } else {
                // 6.1 同步用户的信息
                // console.log(this.userInfo.id, 2222222222222222)
                this.syncUserInfo(this.userInfo)
                this.$router.push('/mine')
                // 6.2 回到主界面
                // this.$router.back()
              }
            } else {
              this.userInfo = {
                message: '登录失败，手机号或验证码不正确！'
              }
            }
          })
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    getCaptcha () {
      this.$refs.captcha.src = 'http://localhost:3000/captcha?time=' + new Date()
    }
  },
  mounted () {
    this.$http.get('/captcha').then(res => {
      // console.log(res.data.text, 555555555555555555555)
      // this.$refs.captcha.src = res.data.text
    })
    // console.log(this.$refs.captcha.src.data)
    // this.getCaptcha()
  }
}
</script>

